import React, { useState, useEffect } from "react";
import axios from "axios";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const SeleccionarFechaHora = () => {
  const [fecha, setFecha] = useState(null);
  const [hora, setHora] = useState("");
  const [horariosBarbero, setHorariosBarbero] = useState([]);
  const [error, setError] = useState("");
  const [mensaje, setMensaje] = useState("");

  const queryParams = new URLSearchParams(window.location.search);
  const barberoId = queryParams.get("barbero_id");
  const usuarioId = 12;
  const corteId = 11;

  useEffect(() => {
    if (barberoId) {
      axios
        .get(`http://localhost:5000/get_horarios/${barberoId}`)
        .then((response) => {
          setHorariosBarbero(response.data);
        })
        .catch(() => {
          setError("Error al obtener los horarios del barbero.");
        });
    }
  }, [barberoId]);

  const handleFechaChange = (date) => {
    setFecha(date);
    setMensaje("");
    setError("");
  };

  const handleHoraChange = (event) => {
    setHora(event.target.value);
    setMensaje("");
    setError("");
  };

  const verificarYGuardarReserva = async () => {
    if (!fecha || !hora) {
      setError("Por favor, selecciona una fecha y una hora.");
      return;
    }
  
    const diasSemana = {
      sunday: "Domingo",
      monday: "Lunes",
      tuesday: "Martes",
      wednesday: "Mi√©rcoles",
      thursday: "Jueves",
      friday: "Viernes",
      saturday: "S√°bado",
    };
  
    const diaSeleccionado = diasSemana[fecha.toLocaleDateString("en-US", { weekday: "long" }).toLowerCase()];
  
    if (!diaSeleccionado) {
      setError("Error al obtener el d√≠a seleccionado.");
      return;
    }
  
    const horarioBarbero = horariosBarbero.find(
      (horario) => horario.dia === diaSeleccionado
    );
  
    if (!horarioBarbero) {
      setError(`No hay horario disponible para el d√≠a ${diaSeleccionado}`);
      return;
    }
  
    const horaInicio = horarioBarbero.hora_inicio.slice(0, 5);
    const horaFin = horarioBarbero.hora_fin.slice(0, 5);
  
    if (hora < horaInicio || hora > horaFin) {
      setError(`La hora seleccionada no est√° dentro del horario disponible (${horaInicio} - ${horaFin}).`);
      return;
    }
  
    // Obtener el token desde el almacenamiento local
    const token = localStorage.getItem("token");
  
    if (!token) {
      setError("No se ha proporcionado el token de autenticaci√≥n.");
      return;
    }
  
    // Enviar la solicitud con el token en la cabecera Authorization
    try {
      const response = await axios.get(
        `http://localhost:5000/guardar_reserva?usuario_id=12&barbero_id=2&corte_id=11&fecha_cita=2025-04-02&hora_cita=11:00&estado=pendiente`, {
          headers: {
            Authorization: `Bearer ${token}`, // Agregar el token a la cabecera Authorization
          },
        }
      );
      console.log("Reserva guardada:", response.data);
    } catch (error) {
      console.error("Error al guardar la reserva:", error);
      setError("Error al guardar la reserva.");
    }
  };
  
  return (
    <div>
      <h1>Seleccionar Fecha y Hora</h1>
      <DatePicker selected={fecha} onChange={handleFechaChange} dateFormat="dd/MM/yyyy" />
      <input type="time" value={hora} onChange={handleHoraChange} />
      {error && <p style={{ color: "red" }}>{error}</p>}
      {mensaje && <p style={{ color: "green" }}>{mensaje}</p>}
      <button onClick={verificarYGuardarReserva}>Guardar</button>
    </div>
  );
};

export default SeleccionarFechaHora;
